<template>
    <div id="app">
        <div class="container-fluid">
            <div class="py-3">
                <h4>
                    Quotas & Groups
                </h4>

                <div v-if="quotas.length > 0">
                    <div class="row">
                        <div class="col-10">
                            <template v-for="(quota, i) in quotas">
                                <QuotaBlock
                                    v-model="quotas[i]"
                                    :no="`${i + 1}`.toString()"
                                    :key="`${i}qb`"
                                    @copy="copyQuota"
                                    @remove="removeQuota"
                                />
                            </template>
                        </div>
                        <div class="col-2">
                            <div class="card sticky-top">
                                <div class="card-body">
                                    <template v-for="(quota, i) in quotas">
                                        <ul :key="`${i}qb`" class="mb-0">
                                            <QuotaTree v-model="quotas[i]" :no="`${i + 1}`.toString()"/>
                                        </ul>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="alert alert-warning" v-else>
                    No Quotas & Groups
                </div>
            </div>

            <div>
                <button @click.prevent="addQuota" class="btn btn-primary  me-1">
                    Add Quota
                </button>

                <button @click.prevent="removeAll" class="btn btn-danger  me-1">
                    Remove All
                </button>
            </div>
        </div>
    </div>
</template>

<script>
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

import QuotaTree from './components/QuotaTree';
import QuotaBlock from './components/QuotaBlock';

export default {
    name: 'App',
    components: {
        QuotaTree,
        QuotaBlock,
    },
    watch: {
        quotas: {
            handler: (value) => {
                window.localStorage.setItem('campaign-create-quotas', JSON.stringify(value));
            },
            deep: true,
        },
    },
    methods: {
        addQuota() {
            this.quotas.push(
                {
                    id: Math.round(Math.random() * 10000000000000),
                    name: '',
                    answer_count: 1000,
                    quotas: [],
                    filters: [],
                },
            );
        },
        copyQuota(quota) {
            let applyId = function(quota)
            {
                quota.id = Math.round(Math.random() * 10000000000000);

                for (let subQuota in quota.quotas)
                {
                    quota.quotas[subQuota] = applyId(quota.quotas[subQuota]);
                }

                return quota;
            }

            let i = this.quotas.indexOf(quota);
            if (i !== -1)
            {
                let copy = JSON.parse(JSON.stringify(quota));

                this.quotas.push(applyId(copy));
            }
        },
        removeQuota(quota) {
            let i = this.quotas.indexOf(quota);
            if (i !== -1)
            {
                this.quotas.splice(i, 1);
            }
        },
        removeAll() {
            this.quotas = [];
        },
    },
    data() {
        return {
            quotas: [],
        };
    },
    created() {
        try
        {
            this.quotas = JSON.parse(window.localStorage.getItem('campaign-create-quotas')) || [];
        }
        catch (e)
        {
            //nothing
        }
    },
};
</script>