<template>
    <div class="mb-3">
        <h3>
            #{{ no }}
        </h3>
        <div class="row">
            <div class="col-8">
                <component
                    :is="`${filter.filter.type}Filter`"
                    :filter="filter.filter"
                    v-model="filter.value"
                />
            </div>
            <div class="col position-relative">
                <div class="position-absolute bottom-0 end-0">
                    <button @click.prevent="$emit('copy', filter)" class="btn btn-success  me-1">
                        Copy Filter
                    </button>

                    <button @click.prevent="$emit('remove', filter)" class="btn btn-danger  me-1">
                        Remove Filter
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import RangeFilter from './filter/RangeFilter';
import SelectFilter from './filter/SelectFilter';

export default {
    props: {
        no: String,
        value: Object,
    },
    components: {
        RangeFilter,
        SelectFilter,
    },
    computed: {
        filter: {
            get() { return this.value; },
            set(val) { this.$emit('input', val); },
        },
    },
};
</script>
