<template>
    <div class="card mb-3 group" :class="quotaClassName" style="background-color: #e7e7e7" :id="`item-${quota.id}`">
        <div class="card-body p-4">
            <h3>
                <span v-if="isQuota">Quota</span><span v-else>Group</span> #{{ no }}
            </h3>

            <div class="row mb-3">
                <div class="col-8">
                    <input type="text" class="form-control" v-model="quota.name" placeholder="Название">
                </div>
                <div class="col">
                    <div class="mb-3">
                        <button @click.prevent="$emit('copy', quota)" class="btn btn-success  me-1">
                            Copy <span v-if="isQuota">Quota</span><span v-else>Group</span>
                        </button>

                        <button @click.prevent="$emit('remove', quota)" class="btn btn-danger  me-1">
                            Remove <span v-if="isQuota">Quota</span><span v-else>Group</span>
                        </button>

                        <div class="dropdown d-inline" v-if="isQuota">
                            <button class="btn btn-secondary" type="button" data-bs-toggle="dropdown">
                                <svg width="14" height="14" focusable="false" data-prefix="fas" data-icon="cog" class="svg-inline--fa fa-cog fa-w-16" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path fill="currentColor" d="M487.4 315.7l-42.6-24.6c4.3-23.2 4.3-47 0-70.2l42.6-24.6c4.9-2.8 7.1-8.6 5.5-14-11.1-35.6-30-67.8-54.7-94.6-3.8-4.1-10-5.1-14.8-2.3L380.8 110c-17.9-15.4-38.5-27.3-60.8-35.1V25.8c0-5.6-3.9-10.5-9.4-11.7-36.7-8.2-74.3-7.8-109.2 0-5.5 1.2-9.4 6.1-9.4 11.7V75c-22.2 7.9-42.8 19.8-60.8 35.1L88.7 85.5c-4.9-2.8-11-1.9-14.8 2.3-24.7 26.7-43.6 58.9-54.7 94.6-1.7 5.4.6 11.2 5.5 14L67.3 221c-4.3 23.2-4.3 47 0 70.2l-42.6 24.6c-4.9 2.8-7.1 8.6-5.5 14 11.1 35.6 30 67.8 54.7 94.6 3.8 4.1 10 5.1 14.8 2.3l42.6-24.6c17.9 15.4 38.5 27.3 60.8 35.1v49.2c0 5.6 3.9 10.5 9.4 11.7 36.7 8.2 74.3 7.8 109.2 0 5.5-1.2 9.4-6.1 9.4-11.7v-49.2c22.2-7.9 42.8-19.8 60.8-35.1l42.6 24.6c4.9 2.8 11 1.9 14.8-2.3 24.7-26.7 43.6-58.9 54.7-94.6 1.5-5.5-.7-11.3-5.6-14.1zM256 336c-44.1 0-80-35.9-80-80s35.9-80 80-80 80 35.9 80 80-35.9 80-80 80z"></path></svg>
                            </button>
                            <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                                <li>
                                    <a class="dropdown-item" href="#" @click.prevent="addQuota">Make As Group</a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>

            <hr>

            <div class="mb-3">
                <div class="row">
                    <div class="col-8">
                        <div class="row mb-3">
                            <div class="col">
                                <h4>
                                    Filters
                                </h4>
                            </div>
                            <div class="col text-end">

                            </div>
                        </div>

                        <div v-if="quota.filters.length > 0" >
                            <div v-for="(filter, i) in quota.filters" :key="`${i}qf`">
                                <QuotaFilter
                                    v-model="quota.filters[i]"
                                    :no="`${i + 1}`.toString()"
                                    @copy="copyFilter"
                                    @remove="removeFilter"
                                />
                            </div>
                        </div>
                        <div class="alert alert-warning" v-else>
                            No Filters
                        </div>

                        <QuotaFilterButton :filters="$root.filters" @select="addFilter">
                            Add Filter To #{{ no }}
                        </QuotaFilterButton>
                    </div>
                    <div class="col">
                        <label class="form-label">
                            Доступно
                        </label>
                        <input type="text" readonly class="form-control-plaintext" value="1000">
                    </div>
                    <div class="col">
                        <div v-if="isQuota">
                            <label class="form-label">
                                Необходимо
                            </label>
                            <input type="text" class="form-control" v-model="quota.answer_count">
                        </div>
                    </div>
                </div>
            </div>

            <div v-if="quota.quotas.length > 0">
                <hr>

                <div class="mb-3">
                    <div class="row mb-3">
                        <div class="col">
                            <h4>
                                Quotas & Groups
                            </h4>
                        </div>
                        <div class="col text-end">

                        </div>
                    </div>

                    <div v-if="quota.quotas.length > 0">
                        <template v-for="(q, i) in quota.quotas">
                            <node v-model="quota.quotas[i]" :no="`${no}_${i + 1}`" :key="`${i}qb`"/>
                        </template>
                    </div>
                    <div class="alert alert-warning" v-else>
                        No Quotas & Groups
                    </div>

                    <button @click.prevent="addQuota" class="btn btn-primary  me-1">
                        Add Quota To #{{ no }}
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import QuotaFilter from './QuotaFilter';
import QuotaFilterButton from './QuotaFilterButton';

export default {
    name: 'node',

    components: {
        QuotaFilter,
        QuotaFilterButton,
    },
    props: {
        no: String,
        value: Object,
    },
    computed: {
        quota: {
            get() { return this.value; },
            set(val) { this.$emit('input', val); },
        },
        quotaClassName: function() {
            if (this.isQuota)
            {
                return 'bg-light Quota';
            }
            return '';
        },
        isQuota: function() {
            return this.quota.quotas.length === 0;
        },
    },
    methods: {
        addQuota() {
            this.quota.quotas.push(
                {
                    id: Math.round(Math.random() * 10000000000000),
                    name: '',
                    answer_count: 1000,
                    quotas: [],
                    filters: [],
                },
            );
        },
        addFilter(filter) {
            this.quota.filters.push(
                {
                    filter: filter,
                    value: null,
                }
            );
        },
        copyFilter(filter) {
            let i = this.quota.filters.indexOf(filter);
            if (i !== -1)
            {
                let copy = JSON.parse(JSON.stringify(filter));

                this.quota.filters.push(copy);
            }
        },
        removeFilter(filter) {
            let i = this.quota.filters.indexOf(filter);
            if (i !== -1)
            {
                this.quota.filters.splice(i, 1);
            }
        },
    },
};
</script>


